import React, {Component} from 'react';
import { Card, CardHeader, CardImg, CardText, CardBody,
    CardTitle, CardSubtitle, Media, Input, Button} from 'reactstrap';
import JSON from 'JSON'
import { FadeTransform } from 'react-animation-components';

//import API_KEY from '../MapsAPI'
import MapComponent from './MapComponent' ;
import GMRComponent from './GMRComponent';

function RenderCard({item}) {

const comments = item.comments.map( (comment) => {
    return (
        <>
            <p ><b>Autor - data</b></p>        
            <p>{comment}</p>
        </>
    )
})

    return(
            <FadeTransform in transformProps={{exitTransform: 'scale(0.5) translateY(-50%)'}}>
                <Card color='success' inverse={false}>
                    <CardHeader ><h4>{item.name}</h4></CardHeader>
                    <CardBody>

                    <div className='row'>

                        <div className='col-12 col-md-6'>
                            <div className='col-12 card-content text-center'>
                                <CardImg style={{width: '100%', height:'50%'}} src={item.img_src} alt={item.name} />
                            </div>
                            <div className='col-12 card-content'>
                                <CardTitle><h5>{item.name}</h5></CardTitle>
                                <CardSubtitle><b>{item.address}</b></CardSubtitle>    
                                <CardText>{'3,5 Km'}</CardText>
                                <CardText>{item.open}</CardText>                            
                            </div>
                        </div>

                        <div className='col-12 col-md-6 card-content'>
                            <div className='col-12 card-content'>
                                <CardTitle><h5 className='text-center'>Informações:</h5></CardTitle>
                            </div>
                            <div className='col-12 card-content'>
                                <CardText>{'Avaliação: ' + item.evaluation}</CardText>        
                                <CardText>{'Produtos: ' + item.products.join(', ')}</CardText>
                                <CardText>{'Qualidade dos produtos: ' + item.quality}</CardText>
                                <CardText>{'Recepção: ' + item.reception}</CardText>
                                <CardText>{'Policiamento: ' + item.police_frequency}</CardText>
                                <CardText>{'Drive-Thru:'}</CardText>
                                <CardText className='text-center'>{item.drive_thru.join(', ')}</CardText>
                            </div>
                        </div>

                        {/* <div className='col-12 col-md-6 card-col'>
                            <br></br>   
                        </div> */}

                        <div className='col-12'>
                            <div className='col-12 card-content'>
                                <CardTitle><h5>Comentários:</h5></CardTitle>
                            </div>
                            <div className='col-12 card-content bg-secondary'>
                                {comments}
                            </div>
                            <div className='col-12 card-content'>
                                <Input type='text' placeholder='Escreva um comentário...'></Input>
                            </div>
                            <div className='col-12 btn' >
                                <Button className='bg-primary' >Comentar</Button> 
                            </div>
                        </div>


                    </div>
                    </CardBody>


                </Card>
            </FadeTransform>

    );
}

class Home extends Component {

    constructor(props) {
        super(props);

        this.state = {
            selectedSpot: this.props.spots[0]
        };
    }
    
    render() {

        const spotlist = this.props.spots.map((item) => {
            return (
                <div className='col-12  col-md-12 card-col'>                    
                    <RenderCard item={item} />
                </div>
            );
        });
    

        return(
            <div className="container">
                
                <div className='row'>               
                    <div className='col-12 col-sm-6'>
                        <div className='row'>
                            <div className='col-12'>
                                <h4 className='title'>Corres perto de você:</h4>
                            </div>
                            <div className='col-12'>
                                <GMRComponent spots={this.props.spots} />
                            </div>

                            <div className='row'>
                                <div className='col-6 text-center vert'>
                                    <div className='col-12 text-center vert'>
                                        <Button className='btn' color='success'>Adicionar localidade</Button>
                                    </div>
                                </div>
                                <div className='col-6 text-center'>
                                    <p>Clique no mapa para adicionar um marcador.</p>
                                    <p>Arraste o marcador para mudar sua posição.</p>
                                    <p>Use o botão ao lado para salvar a localidade.</p>
                                </div>
                            </div>

                            <hr></hr>


                        </div>
                    </div>

                    <div className='col-12 col-sm-6'>
                        <div className='row '>
                            <div className='col-12'>
                                <h4 className='title'>Corre mais próximo:</h4>                    
                            </div>
                            <div className='col-12'>
                                <RenderCard item={this.state.selectedSpot} />
                            </div>
                        </div>
                    </div>                
                </div>

                <h4 className='title'>Corres recomendados:</h4>
                <div className="row row-content">
                    {spotlist}
                </div>

        </div>
        )
    }
}

export default Home;