import React, { Component } from 'react';
import Main from './components/MainComponent';
import { BrowserRouter } from 'react-router-dom';

import './styles/App.css';

class App extends Component {

  render() {
    return (
        
        //<style rel='stylesheet' href='./App.css'/>
        <BrowserRouter>
          <div className="App">
            <Main />
          </div>
        </BrowserRouter>

    );
  }
}

export default App;

    
