import React, { Component } from 'react';
import { Map, GoogleApiWrapper, Marker } from 'google-maps-react';
import API_KEY from '../MapsAPI'
import spots from '../shared/spots';

const style = {
    // width: '400px',
    // height: '100%',
    margin: '0% 5% 5% 5%'
  }
  
const containerStyle = {
    position: 'relative',
    // left: '15%',// '130px',
    // top: '100%',//'15px',  
    width: '100%',
    height: '285px'
}

var options = {
    enableHighAccuracy: true,
    timeout: 5000,
    maximumAge: 0,
};

function success(pos) {
    var crd = pos.coords;

    console.log("Your current position is:");
    console.log(`Latitude : ${crd.latitude}`);
    console.log(`Longitude: ${crd.longitude}`);
    console.log(`More or less ${crd.accuracy} meters.`);

    // this.setState({
    //     center: {
    //         lat: crd.latitude,
    //         lng: crd.longitude
    //     },
    //     user_location: crd
    // });
    return crd;
}

function errors(err) {
    console.warn(`ERROR(${err.code}): ${err.message}`);
}

export class MapContainer extends Component {

    constructor(props) {
        super(props);
    
        this.state = {
            center: null,
            zoom: 14,
            user_location: null
        };
    }
      
    setUserLocation (pos) {
        if (!this.state.user_location) {
            this.setState({
                user_location: {
                    lat: pos.coords.latitude,
                    lng: pos.coords.longitude
                }
            });
            console.log('Localização do usuário alterada: ' + JSON.stringify(this.state.user_location));

        } else {
            console.log('User Localition Already Set.');
        }

    };

    setDefaultMapCenter () {
        if (!this.state.user_location) {
            console.log('Default map center set as default.')
            // this.setState({
            //     center: {
            //         lat: this.props.spots[0].lat,
            //         lng: this.props.spots[0].lng
            //     }
            // });
            this.state.center = {
                        lat: this.props.spots[0].lat,
                        lng: this.props.spots[0].lng
            };
        } else {
            console.log('Default map center set as user_location');
            this.state.center = this.state.user_location
        }
    };

    displayUserPin  = () => {
        if (this.state.user_location) {
            console.log('Marcador da localização do usuário carregado corretamente.')
            return(
                <Marker 
                    key={'userMarker'} id={'userMarker'} 
                    title='SUA LOCALIZAÇÂO'
                    name='User Location'
                    position={this.state.user_location}
                />
            )
            } else {
                return null
            }
    }

    displayMarkers = () => {
        return this.props.spots.map((spot, index) => {
            return (
                <Marker 
                    key={index} id={index} 
                    title={spot.name}
                    name={spot.name}
                    position={{
                        lat: spot.lat,
                        lng: spot.lng
                    }}
                />
            )
        })
    }

    componentDidMount() {
        console.log('Home Component Did Mount')
        //navigator.geolocation.getCurrentPosition(this.setUserLocation.bind(this));

        if (navigator.geolocation) {
            navigator.permissions
            .query({ name: "geolocation" })
            .then(function (result) {
                if (result.state === "granted") {
                    console.log(result.state);
                    //If granted then you can directly call your function here
                    //navigator.geolocation.getCurrentPosition(this.setUserLocation.bind(this));
                } else if (result.state === "prompt") {
                    navigator.geolocation.getCurrentPosition(success, errors, options);
                } else if (result.state === "denied") {
                    //If denied then you have to show instructions to enable location
                }
                result.onchange = function () {
                    console.log(result.state);
                };
            });
        } else {
        alert("Sorry Not available!");
        }
        if (!this.state.user_location) {
            navigator.geolocation.getCurrentPosition(this.setUserLocation.bind(this))
        }
    }

    render() {

        this.setDefaultMapCenter();
        console.log('Rendering...');
        console.log('Centralizando em: ' + JSON.stringify(this.state.center))

        return (

            <Map
                style={style}
                google={this.props.google}
                //initialCenter={this.state.center}
                center={this.state.center}
                zoom={this.state.zoom}
                containerStyle={containerStyle}
            >
                {this.displayMarkers()}
                {this.displayUserPin()}

            </Map>

        );
    } 
}

export default GoogleApiWrapper(
    (props) => ({
        apiKey: API_KEY.key,
    })
)(MapContainer)