import { Navbar, NavbarBrand, Nav, NavbarToggler, Collapse, NavItem,
    Button, Modal, ModalHeader, ModalBody,
    Form, FormGroup, Input, Label, Media } from 'reactstrap';
 
import { NavLink } from 'react-router-dom';
import React, {Component} from 'react';

class Header extends Component {
    
    constructor(props) {        
        super(props);
        this.toggleNav = this.toggleNav.bind(this);
        this.toggleModal = this.toggleModal.bind(this);
        this.handleLogin = this.handleLogin.bind(this);
        
        this.state = {
          isNavOpen: false,
          isModalOpen: false
        };
    }

    toggleNav() {
      this.setState({
        isNavOpen: !this.state.isNavOpen
      });
    }
    toggleModal() {
      this.setState({
        isModalOpen: !this.state.isModalOpen
      });
    }
    handleLogin(event) {
        this.toggleModal();
        alert("Username: " + this.username.value + " Password: " + this.password.value
            + " Remember: " + this.remember.checked);
        event.preventDefault();
    }

    render() {
        return(
            <div>
                <Navbar light expand="md">
                    <NavbarToggler onClick={this.toggleNav} />
                    {/* <NavbarBrand href="/"><img src='assets/images/logo.png' height="30" width="41" alt='Logo' /></NavbarBrand> */}
                    <div className="container">

                        <div className='row'>
                            <div className='col-6'>
                                <Collapse horizontal={true} full={false} isOpen={this.state.isNavOpen} navbar>                                
                                    <Nav navbar>
                                        <NavItem>
                                            <NavLink className="nav-link"  to='/home'><span className="fa fa-home fa-lg"></span> Home</NavLink>
                                        </NavItem>
                                        <NavItem>
                                            <NavLink className="nav-link" to='/aboutus'><span className="fa fa-info fa-lg"></span> About Us</NavLink>
                                        </NavItem>
                                        <NavItem>
                                            <NavLink className="nav-link"  to='/menu'><span className="fa fa-list fa-lg"></span> Menu</NavLink>
                                        </NavItem>
                                        <NavItem>
                                            <NavLink className="nav-link" to='/contactus'><span className="fa fa-address-card fa-lg"></span> Contact Us</NavLink>
                                        </NavItem>
                                    </Nav>
                                </Collapse>
                            </div>
                            <Nav navbar>
                                <NavItem style={{position: 'absolute', right: '5%', top: '8px'}}>
                                    <Button outline onClick={this.toggleModal}><span className="fa fa-sign-in fa-lg"></span> Login</Button>
                                </NavItem>
                            </Nav>                            
                        </div>
                    </div>
                </Navbar>
                
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Login</ModalHeader>
                    <ModalBody>
                        <Form onSubmit={this.handleLogin}>
                            <FormGroup>
                                <Label htmlFor="username">Username</Label>
                                <Input type="text" id="username" name="username"
                                    innerRef={(input) => this.username = input} />
                            </FormGroup>
                            <FormGroup>
                                <Label htmlFor="password">Password</Label>
                                <Input type="password" id="password" name="password"
                                    innerRef={(input) => this.password = input}  />
                            </FormGroup>
                            <FormGroup check>
                                <Label check>
                                    <Input type="checkbox" name="remember"
                                    innerRef={(input) => this.remember = input}  />
                                    Remember me
                                </Label>
                            </FormGroup>
                            <Button type="submit" value="submit" color="primary">Login</Button>
                        </Form>
                    
                    </ModalBody>
                </Modal>

                <div className="md jumbotron" >
                    <div className='container'>
                        <div className="row row-header">
                            <div className="col-12 col-sm-6">
                                <h1>Corre Certo</h1>
                                <p>Descubra o melhor lugar para você fazer o seu corre. Veja avaliações e relatos de usuários sobre a qualidade, os preços e a seguraça em lojinhas de diferentes comunidades.</p>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        );
    }
}

export default Header;